.ReviewThree {
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
  overflow: auto;
}

.ReviewThree-fakeContent {
  width: 100%;
}

.ReviewThree-content {
  padding: 16px;
}

.ReviewThree-block {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: #ffffff;
  margin-bottom: 12px;
  padding: 16px;
}

.ReviewThree-footer {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.ReviewThree-footer-text {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.ReviewThree-footer-text-title {
  font-size: 12px;
  margin-bottom: 8px;
}

.ReviewThree-footer-text-amount {
  font-size: 20px;
  font-weight: bold;
}

.ReviewThree-title {
  font-size: 20px;
  font-weight: bold;
  padding: 32px 16px 16px;
}

.ReviewThree-subtitle {
  font-size: 14px;
  padding: 0 16px 16px;
}

.ReviewThree-services {
  display: flex;
  flex-direction: column;
}

.ReviewThree-service + .ReviewThree-service {
  margin-top: 16px;
}

.ReviewThree-service-name {
  font-size: 16px;
  font-weight: bold;
  padding-bottom: 16px;
}

.ReviewThree-service-date {
  font-size: 14px;
}

.ReviewThree-service-time {
  font-size: 22px;
  font-weight: bold;
  padding: 6px 0;
}

.ReviewThree-service-meta {
  font-size: 12px;
  color: #707070;
}

.ReviewThree-service-tickets {
  display: flex;
  flex-direction: row;
  padding-top: 16px;
}

.ReviewThree-service-tickets-label {
  font-size: 14px;
  padding-right: 24px;
}

.ReviewThree-service-tickets-name {
  font-size: 14px;
  font-weight: bold;
}

.ReviewThree-actions {
  display: flex;
  flex-direction: row;
  border-top: 1px solid #e7e7e7;
  margin-top: 16px;
}

.ReviewThree-seats-group-name {
  font-size: 14px;
  margin-bottom: 2px;
}

.ReviewThree-seats-group-row {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.ReviewThree-seats-group-row-item {
  display: flex;
  flex-direction: column;
  margin-right: 12px;
}

.ReviewThree-seats-group-row-item-title {
  font-size: 12px;
  margin-bottom: 2px;
}

.ReviewThree-seats-group-row-item-value {
  font-size: 24px;
  font-weight: bold;
}

.ReviewThree-seats-group-row-action {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  color: #CE0E2D;
  font-size: 14px;
  flex-grow: 1;
}

.ReviewThree-seats-group-note {
  font-size: 12px;
  color: #707070;
  margin-top: 8px;
}


.ReviewThree-itinerary {
  display: flex;
  flex-direction: column;
  margin: 16px;
}

.ReviewThree-overview {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.15);
  padding: 16px 16px 0 16px;
  border-radius: 4px 4px 0 0;
}

.ReviewThree-overview-0, .ReviewThree-overview-1, .ReviewThree-overview-2 {
  display: flex;
  flex-direction: row;
}

.ReviewThree-overview-0, .ReviewThree-overview-2 {
  justify-content: space-between;
}

.ReviewThree-overview-1 {
margin-top: 8px;
}

.ReviewThree-overview-0-1 {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.ReviewThree-name {
  font-size: 16px;
  line-height: 22px;
  font-weight: bold;
}

.ReviewThree-price {
  font-size: 16px;
  line-height: 22px;
}

.ReviewThree-price-strike {
  font-size: 12px;
  line-height: 12px;
  color: #707070;
  text-decoration: line-through;
  margin-top: 4px;
}

.ReviewThree-item {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.ReviewThree-item + .ReviewThree-item {
  margin-left: 12px;
}

.ReviewThree-item-icon {
  width: 20px;
  fill: #707070;
  margin-right: 4px;
}

.ReviewThree-item-text {
  font-size: 12px;
  line-height: 12px;
  color: #707070;
}

.ReviewThree-action {
  font-size: 12px;
  line-height: 12px;
  padding: 16px 0;
  color: #ce0e2d;
}

.ReviewThree-ticketStitch {
  background-color: #e7e7e7;
  height: 1px;
}

.ReviewThree-service {
  padding: 16px;
  display: flex;
  flex-direction: row;
  background-color: #ffffff;
  box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.15);
}

.ReviewThree-service:last-child {
  border-radius: 0 0 4px 4px;
}

.ReviewThree-service-icon {
  width: 20px;
  fill: #707070;
}

.ReviewThree-service-0, .ReviewThree-service-1 {
  display: flex;
  flex-direction: column;
}

.ReviewThree-service-0 {
  padding-right: 16px;
}

.ReviewThree-service-1 {
  flex-grow: 1;
}

.ReviewThree-service-alert {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 12px;
}

.ReviewThree-service-alert-text {
  font-size: 12px;
  line-height: 12px;
  color: #FD5900;
}

.ReviewThree-service-alert-icon {
  width: 16px;
  fill: #FD5900;
  margin-right: 8px;
}

.ReviewThree-service-date {
  font-size: 14px;
  line-height: 14px;
}

.ReviewThree-service-description {
  font-size: 12px;
  line-height: 12px;
  color: #707070;
  margin-top: 6px;
}

.ReviewThree-stations {
  position: relative;
  display: flex;
  flex-direction: row;
  margin-top: 12px;
  padding-right: 32px;
}

.ReviewThree-station {
  display: flex;
  flex-direction: column;
  flex-basis: 50%;
}

.ReviewThree-station-time {
  font-size: 16px;
  font-weight: bold;
}

.ReviewThree-station-name {
  font-size: 12px;
  margin-top: 4px;
  line-height: 16px;
  color: #707070;
  padding-rigxht: 16px;
}

.ReviewThree-station-arrow {
  position: absolute;
  left: 76px;
  width: 16px;
  margin-right: 8px;
}

.ReviewThree-logo {
  height: 14px;
  margin-right: 8px;
}

.ReviewThree-duration {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 12px;
}

.ReviewThree-duration-text {
  font-size: 12px;
  line-height: 12px;
}