.ServiceResult {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  padding: 0;
  border-radius: 4px;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.15);
}

.ServiceResult-horizontal {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.ServiceResult-badges > * {
  margin-right: 8px;
}

.ServiceResult-details {
  flex-grow: 1;
}

.ServiceResult-prices {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  flex-shrink: 0;
}

.ServiceResult-prices-standard {
  font-weight: bold;
  font-size: 16px;
  line-height: 16px;
}

.ServiceResult-prices-first {
  margin-top: 6px;
  font-size: 12px;
  line-height: 12px;
  color: #707070;
}

.ServiceResult-note {
  font-size: 12px;
  color: #707070;
  padding-top: 8px;
}

.ServiceResult-priceLabel {
  width: 100%;
  font-size: 12px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  color: #707070;
}

.ServiceResult-personIcon {
  fill: #707070;
  height: 12px;
  padding: 0 4px;
}

.ServiceResult-chevron {
  fill: #707070;
  width: 8px;
  flex-shrink: 0;
  margin: 0 12px;
}

.ServiceResult-top {
  align-items: center;
  padding: 16px 0 16px 16px;
}

.ServiceResult-top-content {
  flex-grow: 1;
}

.ServiceResult-stations {
  display: flex;
  flex-direction: row;
  position: relative;
}

.ServiceResult-station {
  display: flex;
  flex-direction: column;
  flex-basis: 50%;
}

.ServiceResult-station-name {
  margin-top: 4px;
  color: #707070;
  font-size: 12px;
  line-height: 16px;
  padding-right: 16px;
}

.ServiceResult-station-arrow {
  position: absolute;
  left: 64px;
  width: 16px;
  height: 16px;
  margin: 0 8px;
}

.ServiceResult-station-time {
  font-size: 16px;
  line-height: 16px;
  font-weight: bold;
}

.ServiceResult-topLine {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 12px;
}

.ServiceResult-bottom {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  border-top: 1px solid #e7e7e7;
}

.ServiceResult-bottom-left {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 16px;
}

.ServiceResult-logo {
  height: 14px;
  margin-right: 12px;
}

.ServiceResult-length {
  flex-grow: 1;
  font-size: 12px;
}

.ServiceResult-info {
  padding: 16px 16px 16px 0;
  color: #ce0e2d;
  font-size: 12px;
  line-height: 12px;
}
