.TicketResult-badges {
  display: flex;
  flex-direction: row;
  padding-bottom: 16px;
}

.TicketResult-badges > * {
  margin-right: 8px;
}

.TicketResult-header {
  width: 100%;
  display: flex;
  flex-direction: row;
}

.TicketResult-actions {
  width: 100%;
  display: flex;
  flex-direction: row;
  border-top: 1px solid #e7e7e7;
  margin-top: 12px;
  padding-top: 12px;
}

.TicketResult-seatsLeft {
  background-color: #707070;
  color: #ffffff;
  padding: 4px 16px;
  font-size: 12px;
}

.TicketResult-note {
  width: 100%;
  font-size: 12px;
  color: #707070;
  display: flex;
  flex-direction: column;
}

.TicketResult-sub {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding-top: 4px;
}

.TicketResult-priceLabel {
  width: 100%;
  font-size: 12px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-bottom: 4px;
  color: #707070;
}

.TicketResult-chevron {
  fill: #707070;
  width: 8px;
  flex-shrink: 0;
  margin: 0 12px;
}

.TicketResult-top {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.TicketResult-topLine {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 12px;
}

.TicketResult-top-content {
  flex-grow: 1;
}

.TicketResult-priceLabel {
  width: 100%;
  font-size: 12px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 4px;
  color: #707070;
}

.TicketResult-personIcon {
  fill: #707070;
  height: 12px;
  padding: 0 4px;
}





.TicketResult {
  display: flex;
  flex-direction: column;
  padding: 0;
  border-radius: 4px;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.15);
}

.TicketResult-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 16px 0 16px 16px;
}

.TicketResult-top-left {
  display: flex;
  flex-direction: column;
  padding-right: 16px;
}

.TicketResult-title {
  font-size: 16px;
  line-height: 16px;
  font-weight: bold;
}

.TicketResult-description {
  display: flex;
  flex-direction: column;
  margin-top: 8px;
}

.TicketResult-description-line {
  font-size: 12px;
  line-height: 12px;
  color: #707070;
}

.TicketResult-description-line + .TicketResult-description-line {
  margin-top: 4px;
}

.TicketResult-bottom {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 16px;
  border-top: 1px solid #e7e7e7;
}

.TicketResult-action {
  font-size: 12px;
  line-height: 12px;
  padding: 16px 0;
  color: #ce0e2d; 
}

.TicketResult-price {
  font-size: 16px;
  line-height: 16px;
}

.TicketResult-nodiscount {
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 4px 0 0 4px;
}

.TicketResult-discount {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #E9F1FA;
  padding: 8px 0 8px 12px;
  border-radius: 4px 0 0 4px;
}

.TicketResult-discount-info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.TicketResult-discount-label {
  color: #005393;
  font-size: 10px;
  line-height: 10px;
  font-weight: bold;
  margin-bottom: 12px;
}

.TicketResult-price-strike {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  background-color: #E9F1FA;
  border-radius: 4px 0 0 4px;
  text-decoration: line-through;
  color: #707070;
  font-size: 12px;
  margin-top: 4px;
}

.TicketResult-alert {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 8px;
}

.TicketResult-alert-icon {
  width: 16px;
  fill: #FD5900;
  margin-right: 8px;
}

.TicketResult-alert-text {
  color: #FD5900;
  font-size: 12px;
  line-height: 12px;
}