.Header {
  width: 100%;
  display: flex;
  position: absolute;
  flex-direction: column;
  background-color: #ce0e2d;
  color: #ffffff;
  z-index: 50;
}

.Header-main {
  height: 64px;
  padding-top: 8px;
  display: flex;
  flex-direction: row;

  justify-content: center;
  background-color: #ce0e2d;
  color: #ffffff;
}

.Header-middle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}

.Header-title {
  font-size: 14px;
  line-height: 14px;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.Header-subtitle {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 12px;
  line-height: 12px;
  margin-top: 6px;
}

.Header-button {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: transparent;
  color: white;
  font-size: 14px;
  border: none;
  padding: 0;
  padding-left: 16px;
  font-family: inherit;
}

.Header-button-icon {
  width: 16px;
  fill: white;
}

.Header-button-text {
  font-size: 14px;
}

.Header-left {
  width: 80px;
  justify-content: flex-start;
}

.Header-right {
  width: 80px;
  justify-content: flex-end;
  padding-right: 16px;
}

.Header-padder-main {
  width: 100%;
  height: 64px;
}

.Header-icon {
  width: 16px;
  fill: #ffffff;
  margin-right: 8px;
}

.Header-sub {
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 16px;
  background-color: #A40B24;
  color: #ffffff;
  font-size: 12px;
}

.Header-sub-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 16px;
}

.Header-sub-item-icon {
  width: 16px;
  fill: #ffffff;
  margin-right: 4px;
}

.Header-sub-item-text {
  font-size: 12px;
}

.Header-padder-sub {
  width: 100%;
  height: 40px;
}