.Badge {
  background-color: #707070;
  border-radius: 4px;
  padding: 2px 6px;
  color: #ffffff;
  font-size: 10px;
  line-height: 12px;
}

.Badge-cheapest {
  background-color: #009A00;
}

.Badge-fastest {
  background-color: #707070;
}

.Badge-recommended {
  background-color: #e7e7e7;
  color: #3A2F2D;
}

